@import '../../static/scss/variables.scss';

.px-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgba(0,0,0,.6);
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 1055;

    .px-modal--dialog {
        display: flex;
        width: auto;
        max-width: 500px;
        min-height: calc(100% - 3.5rem);
        margin: 1.75rem auto;
        align-items: center;
        z-index: 10;
    }

    &.sm .px-modal--dialog {
        max-width: 360px;
    }

    &.lg .px-modal--dialog {
        max-width: 800px;
    }

    &.xl .px-modal--dialog {
        max-width: 1140px;
    }

    .px-modal--content {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        pointer-events: auto;
        border-radius: 16px;
        background-color: #fff;
        background-clip: padding-box;
        outline: 0;
      
        & .modal--header {
          display: flex;
          padding: 24px 28px 28px;
          align-items: center;
          justify-content: space-between;
          flex-wrap: wrap;
      
          & h1 {
            font-size: 22px;
            font-weight: 700;
            color: #212529;
            @include ls-extra-narrow;
          }
      
          & .modal--close {
            width: 3rem;
            height: 3rem;
            color: #fff;
            margin-left: auto;
            padding: 0;
            border: 0;
            border-radius: 50%;
            background-color: #000;
            outline: 0;

            svg {
                width: 20px;
                height: 20px;
            }
          }
        }
      
        & .modal--body {
          padding: 0 28px 28px;

          .notify-text {
            font-weight: 500;
            color: #666;
            @include ls-narrow;
          }
        }      
    }

    .text-center .px-modal--content .modal--header {
        justify-content: center;
    }
}