@import '../../static/scss/variables.scss';

.bottom-sheet {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    margin: auto;
    z-index: 1010;

    .bottom-sheet--backdrop {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.6);
        z-index: 1;
    }

    .bottom-sheet--dialog {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        max-width: 560px;
        max-height: 60vh;
        margin: auto;
        height: 100%;
        border-radius: 20px 20px 0 0;
        background-color: $white;
        animation: bsShow .5s ease;
        z-index: 2;

        .dialog-header {
            padding: 20px 24px;
            
            h1 {
                font-size: 18px;
                font-weight: 600;
                color: #212529;
                @include ls-extra-narrow;
            }
        }

        .dialog-body {
            height: calc(100% - 62px);
            padding: 0 24px 24px;
            overflow-y: auto;

            h3 {
                font-size: 18px;
                font-weight: 700;
                color: #212529;
                letter-spacing: -0.02em;
            }
        }
    }
}

@keyframes bsShow {
    0% {
        bottom: -100%;
    }

    100% {
        bottom: 0;
    }
}