header {position: fixed; top: 0; left: 0; width: 100%; height: 70px; background-color: #fff; box-shadow: 0 2px 8px rgba(0,0,0,.12); z-index: 1000}
#header .logo {font-size: 20px; margin-bottom: 0}
#header .logo a {font-weight: 800; color: #000}
#header .navbar-items ul {font-size: 0}
#header .navbar-items ul li {display: inline-flex}
#header .navbar-items ul li + li {margin-left: 40px}
#header .navbar-items ul li a {font-size: 16px; font-weight: 600; color: #888; letter-spacing: -0.02em}
#header .navbar-items ul li a.active {color: #000}

#container {padding-top: 160px; overflow-x: hidden}

#footer {padding-top: 60px; padding-bottom: 70px; text-align: center; background-color: #f1f3f5}
#footer p {font-size: 14px; font-weight: 500; color: #888; letter-spacing: -0.01em}