$primaryColor: #3e78ff;
$primaryColorHover: darken($primaryColor, 5%);
$primaryColorShadow: rgba($primaryColor, 0.32);
$secondaryColor: #eaeffc;
$secondaryColorHover: darken($secondaryColor, 3%);
$secondaryColorShadow: rgba($secondaryColor, 0.64);
$dangerColor: #e03131;
$dangerColorHover: darken($dangerColor, 5%);
$dangerColorShadow: rgba($dangerColor, 0.32);
$warningColor: #fcc419;
$warningColorHover: darken($warningColor, 5%);
$warningColorShadow: rgba($warningColor, 0.32);
$infoColor: #5c7cfa;
$infoColorHover: darken($infoColor, 3%);
$infoColorShadow: rgba($infoColor, 0.32);
$darkColor: #212529;
$darkColorHover: darken($darkColor, 5%);
$darkColorShadow: rgba($darkColor, 0.32);
$white: #fff;
$black: #000;

:root {
  --primaryColor: #{$primaryColor};
  --primaryColorHover: #{$primaryColorHover};
  --primaryColorShadow: #{$primaryColorShadow};
  --secondaryColor: #{$secondaryColor};
  --secondaryColorHover: #{$secondaryColorHover};
  --secondaryColorShadow: #{$secondaryColorShadow};
  --dangerColor: #{$dangerColor};
  --dangerColorHover: #{$dangerColorHover};
  --dangerColorShadow: #{$dangerColorShadow};
  --warningColor: #{$warningColor};
  --warningColorHover: #{$warningColorHover};
  --warningColorShadow: #{$warningColorShadow};
  --infoColor: #{$infoColor};
  --infoColorHover: #{$infoColorHover};
  --infoColorShadow: #{$infoColorShadow};
  --darkColor: #{$darkColor};
  --darkColorHover: #{$darkColorHover};
  --darkColorShadow: #{$darkColorShadow};
  --white: #{$white};
  --black: #{$black};
}

@mixin ButtonShadow ($color: rgba(0,0,0,.08)) {
  box-shadow: 0 2px 4px 0 $color;
}

@mixin ls-narrow {
  letter-spacing: -0.02em;
}

@mixin ls-extra-narrow {
  letter-spacing: -0.03em;
}

@mixin flexContainer($a: 'center', $b: 'center') {
  display: flex;
  justify-content: #{$a};
  align-items: #{$b};
}

@mixin flexAuto {
  flex: 1 0 0%;
}