@import './variables.scss';

@mixin PrimaryCard {
    height: 100%;
    border: 1px solid #dee2e6;
    border-radius: 12px;
    background-color: #fff;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.06);
}

.section {
    padding-top: 120px;
    padding-bottom: 120px;
}

.todo-list {
    .list-item {
        .todo-card {
            padding: 24px;
            @include PrimaryCard;
            transition: transform 0.3s, box-shadow 0.3s;

            &:hover {
                transform: translateY(-6px);
                box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
            }

            h5 {
                font-size: 18px;
                font-weight: 600;
                color: #333;
                letter-spacing: -0.02em;
                margin-bottom: 16px;
            }

            p {
                font-size: 15px;
                color: #666;
                letter-spacing: -0.015em;
                line-height: 1.7;
            }

            .date {
                font-size: 14px;
                color: #999;
                margin-top: 16px;

                span {
                    display: inline-block;
                    & + span::before {
                        content: '';
                        display: inline-block;
                        width: 3px;
                        height: 3px;
                        margin: 0 8px;
                        border-radius: 3px;
                        background-color: #ccc;
                        vertical-align: 4px;
                    }
                }
            }
        }
    }
}

#writerSheet {
    .writer-list {
        li {
            @include flexContainer('flex-start');
            width: 100%;
            padding: 16px;
            border-radius: 12px;
            cursor: pointer;
            transition: background-color .2s;

            &:hover {
                background-color: #f1f3f5;
            }

            &:active {
                background-color: #e4e8ed;
            }

            .icon {
                width: 40px;
                height: 40px;
                font-size: 24px;
                border-radius: 50%;
                background-color: #f1f3f5;
                @include flexContainer();
            }

            .name {
                padding-left: 16px;
                @include flexAuto;

                h5 {
                    font-size: 16px;
                    font-weight: 600;
                    color: #333;
                    line-height: 1.2;
                    margin-bottom: 2px;
                }

                p {
                    font-size: 13px;
                    color: #868e96;
                }
            }
        }
    }
}